import { Profile } from './Profile/Profile';
import user from '../data/user.json';


export const App =() => {
  return <div> 
    <Profile 
    name={user.name}
   avatar={user.avatar}
   tag={user.tag}
   location={user.location}
   stats={user.stats}

   />
   </div>

}




